.burger-menu {
    width: 25px;
    height: 25px;
    position: absolute;
    right: 25px;
    top: 16px;
    transition: all 200ms ease-in-out;
    cursor: pointer;
    transform: translateX(400%);
    opacity: 0;
    @media (max-width: 600px) {
        transform: translateX(0);
        opacity: 1;
    }
    
}