.app-header {
  background-color: rgb(18, 30, 46);
  color: #fff;
  padding: 10px;
  font-family: 'Varela Round';

  .logo-txt {
    letter-spacing: 0.5px;
    padding: 0;

    .dot-top {
      position: absolute;
      top: 9px;
      left: 73px;
      width: 5px;
      height: 5px;

      @media(max-width: 600px) {
        left: 53px;
      }
    }
  }

  .navbar-wrapper {
    font-size: 1.2rem;
    gap: 10px;
    .close-nav-btn {
      background-color: transparent;
      border: none;
      cursor: pointer;
      transform: translateX(400%);
      filter: invert(40%) sepia(28%) saturate(795%) hue-rotate(180deg)
        brightness(91%) contrast(82%);
      img {
        max-width: 20px;
      }
      position: absolute;
      right: 0;
      top: 5px;
      @media (max-width: 600px) {
        transform: translateX(0);
      }
    }
    nav {
      gap: 10px;
      align-items: center;
      .nav-link {
        transition: 0.3s all;
        position: relative;
        padding-block-end: 3px;

        @media (max-width: 600px) {
          padding-block-end: 0;
          margin-block: 1rem;
        }
        &.active {
          box-shadow: 0 1.5px #f57f1e;
          color: #f57f1e;
        }
        &:not(.active)::after {
          content: '';
          position: absolute;
          bottom: -2px;
          right: 50%;
          display: block;
          min-height: 1.5px;
          width: 0;
          background: #f57f1e;
          transition: width 0.3s ease 0s, left 0.3s ease 0s;
          opacity: 0;
        }
        &:hover::after {
          width: 100%;
          left: 0;
          opacity: 1;
        }
      }
      @media (max-width: 600px) {
        flex-direction: column;
        align-items: baseline;
        justify-content: baseline;
        gap: 18px;
        margin-block-start: 2rem;
      }
    }
    @media (max-width: 600px) {
      position: fixed;
      right: 0;
      top: 0;
      background-color: rgba(0, 0, 0, 0.85);
      height: 100%;
      transform: translateX(400%);
      transition: transform 0.6s, box-shadow 0.6s;
      z-index: 2;
      flex-direction: column;
      align-items: baseline;
      padding-inline: 2rem;
      gap: 18px;
      &.active {
        transform: translateX(0);
      }
    }
  }

  .signout {
    color: rgb(120, 167, 253);
    padding-block-end: 3px;
    @media (max-width: 600px) {
      padding-block-start: 1rem;
    }
  }

  .logo-wrapper {
    display: flex;
    padding-block: 0.25rem;
    user-select: none;
    cursor: pointer;
    .logo {
      max-width: 30px;
      position: relative;
      left: 2.5px;
    }
  }
}
